import React from 'react';
import companyImg from './images/company.png';
import translatingImg from './images/translating.png';
import controlImg from './images/search.png';
import socialMediaImg from './images/social-media.png';
import administrationImg from './images/calendar.png';
import organizationImg from './images/process.png';
import homeImg from './images/home.png';
import accountingImg from './images/accounting.png';
import callImg from './images/call-center.png';
import './Services.css';



function Services(props: {click: boolean, setClick: React.Dispatch<React.SetStateAction<boolean>>}) {
  
  return (
    <div className='services'>
      <div className='services-title main-title'>Služby</div>
      <div className="services-container services-container--desctop">

        {!props.click && <div className='services-home services-box'>
          <img src={homeImg} alt="home" className="services-icon" />
          <div className='title'>Prenájom sídla</div>
          <p>
            Virtuálne sídlo obľubujú hlavne drobní živnostníci či spoločnosti s ručením obmedzeným. Je to
            ideálna alternatíva, ak nemáte fyzickú kanceláriu, alebo prevádzku a nechcete mať túto oficiálnu
            podnikateľskú adresu u Vás doma.
            Toto <span>sídlo sa zapisuje do Obchodného registra </span>spoločnosti (miesto podnikania sa zapisuje do
            Živnostenského registra živnostníka) a daný subjekt ho používa na všetkých úradných dokumentoch.
          </p>
        </div>}

        {!props.click && <div className='services-accounting services-box'>
          <img src={accountingImg} className="services-icon" />
          <div className='title'>Účtovníctvo</div>
          <p>Ponuka <span>vedenia účtovníctva</span> pre fyzické a právnické osoby, <span>spracovanie daňových priznaní</span>,
            ekonomicko – účtovné <span>poradenstvo</span> na elimináciu možných rizík súvisiacich s podnikaním.
            <span> Správa bytových a nebytových objektov</span>, vrátane spracovania ročných vyúčtovaní spotreby energií.</p>
        </div>}

        <div className='services-call services-box' id="cube" onClick={() => props.setClick(!props.click)}>
          <img src={callImg} className="services-icon" />
          <div className='title'>Virtuálna asistentka</div>
          <p><span>Efektívne nahradí zamestnanca</span> vo Vašej firme a <span>ušetrí Vám stovky eur</span>. <span>Preberie za Vás úlohy</span>, ktoré
            nestíhate, alebo neviete a <span>zaplatíte za ňu len toľko, koľko hodín v mesiaci ju reálne potrebujete</span>.
            Nemusíte jej dávať mesačnú mzdu, ani platiť za ňu odvody.
          </p><button className="click" id="click" onClick={() => props.setClick(!props.click)}>{!props.click && `Bližšie informácie` || props.click && `Späť`}</button>
        </div>

        {props.click && <div className="assistant-container">
          <div className="assistant-item company"><img src={companyImg} className='assestant-img' /><p><span className="green">Urady.</span> Vyzdvihnúť / podať rôzne dokumenty na úradoch
            (napr. list vlastníctva, výpis z obchodného registra, žiadosť o
            stavebné povolenie, ...)</p></div>
          <div className="assistant-item administration"><img src={administrationImg} className='assestant-img' /><p><span className="green">Administratíva.</span> Prepísanie textu. Napísanie rôznych žiadostí, oznámení, pozvánok
            Vystavenie faktúr
            ...</p></div>
          <div className="assistant-item social-media"><img src={socialMediaImg} className='assestant-img' /><p><span className="green">Sociálne siete.</span> Správa sociálnych sietí,
            Pomoc so správou webovej stránky
            (napr. dopĺňanie / aktualizovanie produktov, reklamy)</p></div>
          <div className="assistant-item organization"><img src={organizationImg} className='assestant-img' /><p><span className="green">Organizácia.</span> Zabezpečenie školení
            Zorganizovanie pracovných porád
            Rezervácia ubytovania pre obchodných partnerov
            ...</p></div>
          <div className="assistant-item control"><img src={controlImg} className='assestant-img' /><p><span className="green">Kontrola.</span> Strážiť dôležité termíny, aby ste na nič nezabudli
            Kontrola úhrady faktúr
            ...</p></div>
          <div className="assistant-item translating"><img src={translatingImg} className='assestant-img' /><p><span className="green">Preklady.</span> Preloženie písaných textov z / do anglického jazyka</p></div>
        </div>}
      </div>

      <div className="services-container services-container--mobile">

        {<div className='services-home services-box'>
          <img src={homeImg} alt="home" className="services-icon" />
          <div className='title'>Prenájom sídla</div>
          <p className='services-description'>
            Virtuálne sídlo obľubujú hlavne drobní živnostníci či spoločnosti s ručením obmedzeným. Je to
            ideálna alternatíva, ak nemáte fyzickú kanceláriu, alebo prevádzku a nechcete mať túto oficiálnu
            podnikateľskú adresu u Vás doma.
            Toto <span>sídlo sa zapisuje do Obchodného registra </span>spoločnosti (miesto podnikania sa zapisuje do
            Živnostenského registra živnostníka) a daný subjekt ho používa na všetkých úradných dokumentoch.
          </p>
        </div>}

        {<div className='services-accounting services-box'>
          <img src={accountingImg} className="services-icon" />
          <div className='title'>Účtovníctvo</div>
          <p className='services-description'>Ponuka <span>vedenia účtovníctva</span> pre fyzické a právnické osoby, <span>spracovanie daňových priznaní</span>,
            ekonomicko – účtovné <span>poradenstvo</span> na elimináciu možných rizík súvisiacich s podnikaním.
            <span> Správa bytových a nebytových objektov</span>, vrátane spracovania ročných vyúčtovaní spotreby energií.</p>
        </div>}

        <div className='services-call services-box' id="cube" onClick={() => props.setClick(!props.click)}>
          <img src={callImg} className="services-icon" />
          <div className='title'>Virtuálna asistentka</div>
          <p className='services-description'><span>Efektívne nahradí zamestnanca</span> vo Vašej firme a <span>ušetrí Vám stovky eur</span>. <span>Preberie za Vás úlohy</span>, ktoré
            nestíhate, alebo neviete a <span>zaplatíte za ňu len toľko, koľko hodín v mesiaci ju reálne potrebujete</span>.
            Nemusíte jej dávať mesačnú mzdu, ani platiť za ňu odvody.
          </p><button className="click" id="click" onClick={() => props.setClick(!props.click)}>{!props.click && `Bližšie informácie` || props.click && `Späť`}</button>
          {props.click && <div className="assistant-container">
          <div className="assistant-item company"><img src={companyImg} className='assestant-img' /><p className='services-description'><span className="green">Urady.</span> Vyzdvihnúť / podať rôzne dokumenty na úradoch
            (napr. list vlastníctva, výpis z obchodného registra, žiadosť o
            stavebné povolenie, ...)</p></div>
          <div className="assistant-item administration"><img src={administrationImg} className='assestant-img' /><p className='services-description'><span className="green">Administratíva.</span> Prepísanie textu. Napísanie rôznych žiadostí, oznámení, pozvánok
            Vystavenie faktúr
            ...</p></div>
          <div className="assistant-item social-media"><img src={socialMediaImg} className='assestant-img' /><p className='services-description'><span className="green">Sociálne siete.</span> Správa sociálnych sietí,
            Pomoc so správou webovej stránky
            (napr. dopĺňanie / aktualizovanie produktov, reklamy)</p></div>
          <div className="assistant-item organization"><img src={organizationImg} className='assestant-img' /><p className='services-description'><span className="green">Organizácia.</span> Zabezpečenie školení
            Zorganizovanie pracovných porád
            Rezervácia ubytovania pre obchodných partnerov
            ...</p></div>
          <div className="assistant-item control"><img src={controlImg} className='assestant-img' /><p className='services-description'><span className="green">Kontrola.</span> Strážiť dôležité termíny, aby ste na nič nezabudli
            Kontrola úhrady faktúr
            ...</p></div>
          <div className="assistant-item translating"><img src={translatingImg} className='assestant-img' /><p className='services-description'><span className="green">Preklady.</span> Preloženie písaných textov z / do anglického jazyka</p></div>
        </div>}
  
        </div>

            </div>
  
    </div>
  );
}

export default Services;
