import React, { useState } from "react";
import Menu from "./Menu";
import Foot from "./Foot";
import Formular from "./Formular";
import yes from "./images/yes.png";
import Ad from "./Ad";
import CookieConsent from "react-cookie-consent";


function MediumPackagePage(){

  const [isOrdered, setIsOrdered] = useState(false);
  const [mobileMenu, setMobileMenu] = useState(false);
  
  return(
    <div>
      <Menu mobileMenu={mobileMenu} setMobileMenu={setMobileMenu}/>
      <div className={!isOrdered? "main-title title-margin" : "enable-display"}>Stredný balík</div>
      <div className={!isOrdered? "content-box": "enable-display"}>
        <div className="summary-box">
          <div className="summary-list">
            <ul className='services-list summary-01'>
                <li className='services-item'>Súhlas so zriadením sídla / miesta podnikania **</li>
                <li className='services-item'>Označenie poštovej schránky</li>
                <li className='services-item'>Preberanie listových zásielok (obyčajných)</li>
                <li className='services-item'>Archivácia neprevzatých dokladov max. 1 rok</li>
                <li className='services-item'>Archivácia neprevzatých dokladov max. 1 rok</li>
                <li className='services-item services-item--long'>Preposlanie listových zásielok mailom<br/>(neotvorené)– 1x týždenne</li>
                <li className='services-item services-item--last'>Cena spolu / mesiac</li>
                <li className='services-item services-item--last'>Cena spolu / rok</li>
              </ul>

              <ul className='check-list-01 summary-02'>
                <li className='check-item '><img className='check-img' src={yes} /></li>
                <li className='check-item '><img className='check-img' src={yes} /></li>
                <li className='check-item'><img className='check-img' src={yes} /></li>
                <li className='check-item'><img className='check-img' src={yes} /></li>
                <li className='check-item'><img className='check-img' src={yes} /></li>
                <li className='check-item services-item--long'><img className='check-img' src={yes} /></li>
                <li className='check-item check-item--last'>8 &euro;</li>
                <li className='check-item check-item--last'>96 &euro;</li>
              </ul>
          </div>
        </div>
        <Formular package="základný" isOrdered={isOrdered} setIsOrdered={setIsOrdered}/>
      </div>
      <div className={isOrdered? "main-title title-center": "enable-display"}>Ďakujeme za objednávku</div>
      <p className={isOrdered? "title text-center": "enable-display"}>ozveme sa Vám do troch pracovných dní.</p>
      <Ad/>
      <Foot bottom={isOrdered}/>
      <CookieConsent
        location="bottom"
        buttonText="Rozumiem"
        disableStyles={true}
        style={{ background: "#2B373B", color: "var(--main-color-orange)", textAlign: "center", display: "flex", justifyContent: "center", position: "fixed", width: "100%", height: "3rem", alignItems: "center", padding: "0.1rem" }}
        buttonStyle={{ backgroundColor: "var(--main-color-orange)", textAlign: "center", color: "var(--main-color-brown)", fontSize: "1rem", width: "5.6rem", height: "1.7rem", border: "none", borderRadius: "0.5rem", margin: "0.7rem", marginLeft: "1rem", cursor: "pointer"  }}
        expires={365}
        >
        Používaním našich stránok vyjadrujete súhlas s tým, že používame súbory cookie.
      </CookieConsent>
    </div>
  )
};

export default MediumPackagePage;