import React, { useState } from 'react';
import { useNavigate } from "react-router-dom";
import yes from './images/yes.png';
import no from './images/no.png';
import './PriceList.css';
import { handleClickScrollToTop } from './App';

function PriceList(props: {click: boolean}) {
  const navigate = useNavigate();

  const navigateToPage = (url: string) => {
    navigate(url);
    handleClickScrollToTop();
  };

  return (
      <div className='prices'>
        <div className='prices-title main-title'>Cenník</div> 
        {!props.click && 
        <div>
        <div className='prices-box'>
          <ul className='services-list'>
            <li className='services-item services-item--first'>Balík služieb súvisiacich s prenájmom sídla *</li>
            <li className='services-item'>Súhlas so zriadením sídla / miesta podnikania **</li>
            <li className='services-item'>Označenie poštovej schránky</li>
            <li className='services-item'>Preberanie listových zásielok (obyčajných)</li>
            <li className='services-item'>Archivácia neprevzatých dokladov max. 1 rok</li>
            <li className='services-item'>Preposlanie listových zásielok mailom (neotvorené) – 1x týždenne</li>
            <li className='services-item'>Preposlanie lístku o uložení zásielky na pošte mailom – 1x týždenne</li>
            <li className='services-item'>Preposlanie listových zásielok na dohodnutú adresu – 2x mesačne (doporučene)</li>
            <li className='services-item'>Preposlanie obsahu listových zásielok mailom (otvorené) – 1x týždenne</li>
            <li className='services-item services-item--last'>Cena spolu / mesiac</li>
          </ul>

          <ul className='check-list-01'>
            <li className='check-item check-item--first'>Základný</li>
            <li className='check-item '><img className='check-img' src={yes} /></li>
            <li className='check-item '><img className='check-img' src={yes} /></li>
            <li className='check-item'><img className='check-img' src={yes} /></li>
            <li className='check-item'><img className='check-img' src={yes} /></li>
            <li className='check-item'><img className='check-img' src={no} /></li>
            <li className='check-item'><img className='check-img' src={no} /></li>
            <li className='check-item'><img className='check-img' src={no} /></li>
            <li className='check-item'><img className='check-img' src={no} /></li>
            <li className='check-item check-item--last'>5 &euro;</li>
            <li className='check-item'><button className='click check-click' onClick={ () => navigateToPage("/formular-zakladny")}>Mám záujem</button></li>
          </ul>

          <ul className='check-list-02'>
            <li className='check-item check-item--first'>Stredný</li>
            <li className='check-item'><img className='check-img' src={yes} /></li>
            <li className='check-item'><img className='check-img' src={yes} /></li>
            <li className='check-item'><img className='check-img' src={yes} /></li>
            <li className='check-item'><img className='check-img' src={yes} /></li>
            <li className='check-item'><img className='check-img' src={yes} /></li>
            <li className='check-item'><img className='check-img' src={yes} /></li>
            <li className='check-item'><img className='check-img' src={no} /></li>
            <li className='check-item'><img className='check-img' src={no} /></li>
            <li className='check-item check-item--last'>8 &euro;</li>
            <li className='check-item'><button className='click check-click' onClick={ () => navigateToPage("/formular-stredny")}>Mám záujem</button></li>
          </ul>

          <ul className='check-list-03'>
            <li className='check-item check-item--first'>Rozšírený</li>
            <li className='check-item'><img className='check-img' src={yes} /></li>
            <li className='check-item'><img className='check-img' src={yes} /></li>
            <li className='check-item'><img className='check-img' src={yes} /></li>
            <li className='check-item'><img className='check-img' src={yes} /></li>
            <li className='check-item'><img className='check-img' src={yes} /></li>
            <li className='check-item'><img className='check-img' src={yes} /></li>
            <li className='check-item'><img className='check-img' src={yes} /></li>
            <li className='check-item'><img className='check-img' src={yes} /></li>
            <li className='check-item check-item--last'>13 &euro;</li>
            <li className='check-item'><button className='click check-click' onClick={ () => navigateToPage("/formular-rozsireny")}>Mám záujem</button></li>
          </ul>

        </div>
        <p className='prices-ps'>* Platí sa paušálne ročne, vopred</p>
        <p className='prices-ps'>** Registrácia sídla / miesta podnikania min. na 12 mesiacov</p>
        </div>}
  

      <div className='assistantPrice-box'>
        <div className='assistantPrice-item services-item--first'>Vedenie učtovníctva a služby virtuálne asistentky</div>
        <div className='assistantPrice-item'>Cenník je stanovený individuálne po zadaní úloh a zistenia náročnosti práce ***</div>
        <div className='assistantPrice-item assistanPrice-item--last'>V prípade záujmu o služby virtuálnej asistentky nás neváhajte kontaktovať na tel. čísle: <span>+421 907 149 549</span></div>
      </div>
      <p className='prices-ps' id="quotes">*** Ak služba podlieha nákladom vynaložených predom (napr. poplatok na katastri), tieto poplatky su vybrané od klienta predom. V ostatných prípadoch je faktúra dodaná až po vykonaní prác.</p>
    </div>

  )
}

export default PriceList;